import { Component, AfterViewInit, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { animate, state, style, transition, trigger } from '@angular/animations';
import { Order } from '../shared/models/order';
import { OrderService } from '../shared/services/orderService'
import { CommonModule, DatePipe } from '@angular/common';
import { MatPaginator, MatPaginatorIntl, MatSort, MatTableDataSource } from '@angular/material';

@Component({
	selector: 'app-orders',
	templateUrl: './orderList.component.html',
	styleUrls: ['./orderList.component.scss'],
	animations: [
		trigger('detailExpand', [
			state('collapsed', style({ height: '0px', minHeight: '0' })),
			state('expanded', style({ height: '*' })),
			transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
		]),
	],
})
export class OrderListComponent implements OnInit {

	ngAfterViewInit() {
		this.dataSource.paginator = this.paginator;
		this.dataSource.paginator._intl.itemsPerPageLabel = 'Items per page';
		this.dataSource.paginator._intl.previousPageLabel = 'Previous page';
		this.dataSource.paginator._intl.nextPageLabel = 'Next page';
	}

	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort, { static: true }) sort: MatSort;
	resultsLength = 0;
	columnsToDisplay = ['orderID', 'date', 'businessName', 'appliedCredit', 'billedCharges', 'orderTotal', 'details'];
	expandedOrder = new Order();
	orders = new Array<Order>();
	dataSource = new MatTableDataSource<Order>();
	packers = new Array<string>();
	groups = new Array<string>();
	franchiseApies = new Array<string>();
	dropShipApies = new Array<string>();
	storeNumbers = new Array<string>();
	states = new Array<string>();
	payments = new Array<string>();
	timeframes = new Array<string>();
	stores = new Array<string>();
	coupons = new Array<string>();
	statuses = new Array<string>();
	last = '';
	packer = '';
	group = '';
	franchiseApi = '';
	dropShipApi = '';
	storeNumber = '';
	state = '';
	payment = '';
	timeFrame = '';
	store = '';
	coupon = '';
	status = '';
	dateTo = '';
	dateFrom = '';
	search = '';
	checkbox = false;
	constructor(public orderService: OrderService) {

	}
	ngOnInit() {

		this.orders = this.orderService.orders;
		this.dataSource.data = this.orders;
		this.dataSource.paginator = this.paginator;
		this.dataSource.sort = this.sort;
		this.packers = this.orderService.packers;
		this.groups = this.orderService.groups;
		this.franchiseApies = this.orderService.franchiseApies;
		this.dropShipApies = this.orderService.dropShipApies;
		this.storeNumbers = this.orderService.storeNumbers;
		this.states = this.orderService.states;
		this.payments = this.orderService.payments;
		this.timeframes = this.orderService.timeframes;
		this.stores = this.orderService.stores;
		this.coupons = this.orderService.coupons;
		this.statuses = this.orderService.statuses;
		this.resultsLength = this.orders.length;
	}
	applyFilter(event: Event) {
		const filterValue = (event.target as HTMLInputElement).value;
		this.dataSource.filter = filterValue.trim().toLowerCase();
	  }
	orderStatusFilter(event: any) {
		if (event.value) {
			let array = new Array<Order>();
			array = this.orders.filter(res => res.orderStatus == event.value)
			this.dataSource.data = array;
		} else {
			this.dataSource.filter = '';
		}
	}

	storeFilter(event: any) {
		if (event.value) {
			let array = new Array<Order>();
			array = this.orders.filter(res => res.store == event.value)
			this.dataSource.data = array;
		} else {
			this.dataSource.filter = '';
		}
	}

	paymentStatusFilter(event: any) {
		if (event.value) {
			let array = new Array<Order>();
			array = this.orders.filter(res => res.paymentStatus == event.value)
			this.dataSource.data = array;
		} else {
			this.dataSource.filter = '';
		}
	}

	timeFrameFilter(event: any) {
		let today = new Date();
		let yesterday = today.getUTCDate() - 2;
		let array = new Array<Order>();
		if (event.value) {
			if (event.value == 'Today') {
				console.log(today ,"today")
				
				array = this.orders.filter(res => res.date.getUTCDate() == today.getUTCDate())
			}
			if (event.value == 'Yesterday') {
				array = this.orders.filter(res => res.date.getUTCDate() == yesterday)
			}
			this.dataSource.data = array;
		} else {
			this.dataSource.filter = '';
		}
	}

	dateFromFilter(event: any) {
		let array = new Array<Order>();
		if(event.value) {
			array = this.orders.filter(res => event.value <= res.date);
			this.dataSource.data = array;
		} else {
			this.dataSource.filter = '';
		}
	}
	dateToFilter(event: any) {
		let array = new Array<Order>();
		if(event.value) {
			array = this.orders.filter(res => event.value >= res.date);
			this.dataSource.data = array;
		} else {
			this.dataSource.filter = '';
		}
	}
	refreshInputs() {
		this.last = '';
		this.packer = '';
		this.group = '';
		this.franchiseApi = '';
		this.dropShipApi = '';
		this.storeNumber = '';
		this.state = '';
		this.payment = '';
		this.timeFrame = '';
		this.store = '';
		this.coupon = '';
		this.status = '';
		this.dateTo = '';
		this.dateFrom = '';
		this.search = '';
		this.checkbox = false;
		this.dataSource = new MatTableDataSource<Order>();
		this.dataSource.data = this.orderService.orders;
		this.dataSource.paginator = this.paginator;
		this.dataSource.sort = this.sort;
	}

}
