import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { MatTabChangeEvent, MatTableDataSource } from '@angular/material';
import { ActivatedRoute, Router } from '@angular/router';
import { Order } from 'app/shared/models/order';
import { ActivityLog, OrderService, Product } from 'app/shared/services/orderService';
import { animate, state, style, transition, trigger } from '@angular/animations';


@Component({
    selector: 'app-order-detail',
    templateUrl: './orderDetail.component.html',
    styleUrls: ['./orderDetail.component.scss'],
    animations: [
        trigger('detailExpand', [
            state('collapsed', style({ height: '0px', minHeight: '0' })),
            state('expanded', style({ height: '*' })),
            transition('expanded <=> collapsed', animate('225ms cubic-bezier(0.4, 0.0, 0.2, 1)')),
        ]),
    ],
})
export class OrderDetailComponent implements OnInit, AfterViewInit {

    displayedColumns: string[] = ['from', 'message', 'createdAt'];
    displayedColumnsLogs: string[] = ['eventType', 'date', 'user', 'details', 'ip', 'agent'];
    router: Router;
    activeRouter: ActivatedRoute;
    order?= new Order();
    id: string;
    orderStatus?: string = '';
    statuses: string[];
    courier?: string = 'UPS';
    noteMessage?: string = '';
    notes?: Note[] = new Array<Note>();
    dataSource = new MatTableDataSource<Note>();
    expandedOrder: Note;
    isEdit: boolean = false;
    noteToEdit: number;
    isDisabled: boolean = true;
    tabIndex: number = 0;
    logs: ActivityLog[] = new Array<ActivityLog>();
    logDataSource = new MatTableDataSource<ActivityLog>();
    eventTypes = ['OrderUpdated', 'OrderNoteCreated', 'OrderCreated']
    productsDataSource = new MatTableDataSource<Product>();
    displayedColumnsProd: string[] = ['item', 'cost', 'quantity', 'total'];
    products: Product[] = new Array<Product>();
    shippingCouriers: string[];
    @ViewChild('tabGroup') tabGroup;

    constructor(private _router: Router,
        private _activeRouter: ActivatedRoute,
        public orderService: OrderService) {
        this.router = _router;
        this.activeRouter = _activeRouter;
    }
    ngOnInit() {
        this.id = this.activeRouter.snapshot.params['id'];
        this.order = this.orderService.orders.find(a => a.orderID == this.id);
        this.statuses = this.orderService.statuses;
        this.orderStatus = this.order?.orderStatus;
        this.logDataSource.data = this.orderService.logs;
        this.productsDataSource.data = this.orderService.products;
        this.shippingCouriers = this.orderService.couriers;
    }

    ngAfterViewInit() {
        // this.tabIndex = 0;
    }

    tabChanged(tabChangeEvent: MatTabChangeEvent): void {
        this.tabIndex = tabChangeEvent.index;
    }

    applyFilter(event: any) {
        if (event.value) {
            const filterValue = event.value;
            this.logDataSource.filter = filterValue.trim().toLowerCase();
        } else {
            this.logDataSource.filter = '';
        }
    }

    goBack() {
        this.router.navigateByUrl('/orderlist')
    }

    saveNote(noteMessage: string, isEdit: boolean) {
        if (!isEdit) {
            var id = this.notes?.length;
            if (id) {
                id = id + 1;
            } else {
                id = 1;
            }
            var note: Note = {
                'noteId': id,
                'date': new Date(),
                'noteMessage': noteMessage,
                'writterName': 'Mike'
            }
            this.notes?.push(note);
            this.dataSource = new MatTableDataSource<Note>();
            this.dataSource.data = this.notes;
            this.noteMessage = '';
        } else {

            this.notes.forEach(res => {
                if (res.noteId == this.noteToEdit) {
                    res.noteMessage = noteMessage;
                }
            });
            this.dataSource.data = this.notes;
            this.isEdit = false;
            this.noteMessage = '';
        }
    }

    deleteNote(note: Note) {
        var index = this.notes.indexOf(note);
        this.notes.splice(index, 1)
        console.log(this.notes);
        this.dataSource.data = this.notes;
    }
    editNote(note: Note) {
        this.noteMessage = note.noteMessage;
        this.isEdit = true;
        this.noteToEdit = note.noteId;
    }

    cancelEdit() {
        this.noteMessage = '';
        this.isEdit = false;
    }

    editAddress() {
        this.isDisabled = false;
    }

    cancelAddress() {
        this.isDisabled = true;
    }
}
export interface Note {
    noteId?: number;
    noteMessage?: string;
    writterName?: string;
    date?: Date;
}

